.testing {
  width: 71.42857%; }

#main {
  width: 97%;
  background-image: url(../img/bg.jpg);
  background-repeat: no-repeat;
  opacity: .4;
  width: 100%;
  height: 100%;
  -webkit-transition: width 1s ease-in;
  -o-transition: width 1s ease-in;
  transition: width 1s ease-in;
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0; }

p, div {
  font-size: 2em; }
  p a, div a {
    font-weight: bold; }

pre {
  font-size: 3em;
  -webkit-transition: width 1s ease-in;
  -o-transition: width 1s ease-in;
  transition: width 1s ease-in; }
